/* stylelint-disable declaration-block-no-redundant-longhand-properties */
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: 7.6vh;
  background-color: #FFF;
  padding-right: 1rem;
  font-size: 1.4rem;
}

.navbar ul {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}

.navbar li {
  margin: 0 2rem 0 0;
  cursor: pointer;
}

.navbar ul a {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-size: 1.6rem;
  line-height: 1.8rem;
  margin: 0 1rem 0 0;
  cursor: pointer;
}

.navbar ul a:hover {
  text-decoration: underline;
}

.page {
  z-index: -1;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: left;
  margin: 0;
}

.background-elements {
  z-index: 10;
}

.background-text p {
  overflow: hidden;
  position: absolute;
  top: 22vw;
  left: 75vw;
  width: 18vw;
  opacity: 0.86;
  font-size: 1.8rem;
  transform: rotate(18deg);
  font-weight: 500;
  animation-name: slow2;
  animation-duration: 4s;
  @media (max-width: 1220px) {
    font-size: 1rem;
    }
}

.background-text p:hover {
  cursor: pointer;
}

.arrow-image {
  z-index: 10;
  position: absolute;
  opacity: 0.86;
  top: 10vw;
  left: 80vw;
  width: 16vw;
  height: auto;
  animation: slow 1.6s;
  overflow: hidden;
  transform: rotate(-8deg);
}

.arrow-image:hover {
  opacity: 1;
  cursor: pointer;
}

@keyframes slow {
  0%   { overflow: hidden; width: 0; opacity: 0; transform: translate(150%) skewX(-30deg) skewY(0deg) ; }
  40%  { overflow: hidden; opacity: 0.4; transform: translate(35%) skewX(-30deg) skewY(0deg); }
  70%  { overflow: hidden; transform: translate(0%)   skewX(-30deg) skewY(-2deg); }
  80%  { overflow: hidden; transform: translate(0%)   skewX(20deg) skewY(-4deg); }
  95%  { overflow: hidden; transform: translate(0%)   skewX(-10deg) skewY(-6deg); }
  100% { overflow: hidden; transform: translate(0%)   skewX(0deg)  skewY(-8deg); }
}

@keyframes slow2 {
  0%   { overflow: hidden; opacity: 0; transform: scale(0.6) translate(150%) skewX(-30deg) skewY(0deg); }
  70%  { overflow: hidden; transform: scale(0.8) translate(0%)   skewX(-30deg) skewY(6deg); }
  80%  { overflow: hidden; transform: scale(1) translate(0%)   skewX(20deg) skewY(12deg); }
  85%  { overflow: hidden; transform: scale(1) translate(0%)   skewX(10deg) skewY(16deg); }
  100% { overflow: hidden; transform: scale(1) translate(0%)   skewX(0deg) skewY(18deg); }
}

#home {
  display: flex;
  flex-direction: column;
  margin-top: 16vw;
}

#home h1 {
  line-height: 10rem;
  margin-left: 10vw;
  margin-right: 16vw;
  font-size: 4rem;
}

#home p {
  margin-left: 10vw;
  margin-right: 16vw;
  font-size: 2rem;
}

#about {
  display: flex;
  flex-direction: row;
  margin-top: 16vw;
  flex-wrap: none;
  flex: 1 auto;
  gap: 8rem;
  justify-content: center;
  align-items: flex-start;
}

#about h1 {
  margin-left: 10vw;
  margin-right: 10vw;
  font-size: 4rem;
}

#about p {
  font-size: 2rem;
  margin-left: 10vw;
}

#about .right {
  margin-top: 2rem;
  margin-right: 14vw;
}

#about .about-img {
  width: 26rem;
  height: 26rem;
  border-radius: 100rem;
}

#about .about-btn {
  display: flex;
  flex-direction: row;
  justify-content: left;
  margin-left: 14vw;
  margin-top: 4rem;
  margin-bottom: 0;
  gap: 2rem;
  z-index: 10;
}

#about .about-btn button {
  z-index: 10;
  opacity: 0.84;
  font-weight: 600;
  font-size: 1.6rem;
  font-weight: 500;
  line-height: 2rem;
  padding: 0.5rem 1rem;
  background-color: rgb(255, 255, 255, 0);
  border-radius: 0.2rem;
  border: solid;
  outline: none;
  min-width: 12rem;
  min-height: 3.6rem;
}

#about .about-btn button:hover {
  transform: scale(1.01) rotate(-359.2deg);
  opacity: 1;
  cursor: pointer;
}

#about .arrow-about {
  z-index: 10;
  opacity: 0.82;
  margin-left: 20vw;
  margin-top: -6vh;
  width: 18rem;
  height: 18rem;
  transform: rotate(-20deg);
}

#about .arrow-about:hover {
  opacity: 1;
  cursor: pointer;
}

#projects {
  display: flex;
  flex-direction: column;
  margin-top: 12rem;
}

#projects .title h1 {
  margin-left: 10vw;
  margin-right: 10vw;
  font-size: 4rem;
}

#projects .title p {
  font-size: 2rem;
  margin-left: 10vw;
  margin-right: 16vw;
}

#projects .containers {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  margin: 8rem 2.2rem;
  padding: 0;
  grid-gap: 2.2rem;
  text-align: center;
  align-items: center;
}

#projects .containers .container {
  display: flex;
  flex-direction: column;
  z-index: 10;
  min-height: 32rem;
  padding: 2rem 1.2rem 2rem 1.2rem;
  border-radius: 0.6rem;
	transition: all 0.3s;
	position: relative;
  cursor: pointer;
  gap: auto;
  justify-content: space-between;
}

#projects .container span {
	transition: all 0.3s;
}

#projects .container::before {
	content: '';
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 1;
	opacity: 0;
	transition: all 0.3s;
	border-top-width: 0.2rem;
	border-bottom-width: 0.2rem;
	border-top-style: solid;
	border-bottom-style: solid;
	border-top-color: rgba(255,255,255,0.5);
	border-bottom-color: rgba(255,255,255,0.5);
	transform: scale(0.1, 1);
}

#projects .container:hover span {
	letter-spacing: 2px;
}

#projects .container:hover::before {
	opacity: 1;	
	transform: scale(1, 1);	
  background-size: 100%;
  background-repeat: no-repeat;
  z-index: 5;
}

#projects .container::after {
	content: '';
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 1;
	transition: all 0.4s;
  background-color: rgb(255, 255, 255, 0.2);
}

#projects .containers .container:hover::after {
	opacity: 0;	
	transform: scale(0.1, 1);
}

#projects .container h3 {
  font-weight: 800;
  font-size: 2.6rem;
  margin: 0;
  margin-bottom: 1.2rem;
}

#projects .container p {
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 2rem;
  margin: 0rem 0.8rem 0rem 0.8rem;
}

#projects .used-technologies {
  display: flex;
  flex-direction: row;
  gap: 4rem;
  margin-top: 0.2rem;
  margin-bottom: 1.4rem;
  margin-left: 0;
  margin-right: 0;
  padding: 0;
  justify-content: center;
  font-size: 1.6rem;
}

.used-technologies .technology {
  display: flex;
  flex-direction: column;
}

.used-technologies .technology #icon {
  font-size: 4rem;
  margin: 0;
  margin-bottom: 0.4rem;
  align-self: center;
}

.used-technologies  .technology .text {
  text-align: center;
  margin: 0;
}

#projects .project-btn {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin-top: 1.2rem;
  margin-bottom: 0rem;
  z-index: 10;
}

#projects .project-btn button {
  z-index: 10;
  font-weight: 600;
  font-size: 1.6rem;
  line-height: 2rem;
  padding: 0.5rem 1rem;
  background-color: rgba(255,255,255,0.9);
  box-shadow: rgba(17, 17, 26, 0.1) 0px 4px 16px, rgba(17, 17, 26, 0.1) 0px 8px 24px, rgba(17, 17, 26, 0.1) 0px 16px 56px;
  border-radius: 0.2rem;
  border: none;
  outline: none;
  min-width: 12rem;
  min-height: 3.6rem;
}

#projects .project-btn button:hover {
  transform: scale(1.01) rotate(-359.2deg);
  opacity: 1;
  cursor: pointer;
}

@media (max-width: 1800px) {
  #projects .containers {
    margin: 6rem 1.2rem;
    padding: 0;
    grid-gap: 1.2rem;
  }
  #projects .containers .container {
  min-height: 30rem;
  }
}

@media (max-width: 1440px) {
  #projects .containers {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    margin: 4rem 0.8rem;
    grid-gap: 0.8rem;
  }
  
  #projects .containers .container {
  min-height: 36rem;
  }

  .background-elements .arrow-image {
    width: 12rem;
    height: 12rem;
  }
}

@media (max-width: 1000px) {
  #projects .containers {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    margin: 2rem 0.8rem;
    grid-gap: 2rem;
  }
}

@media (max-width: 1280px) {
  .background-elements .arrow-image {
    width: 8rem;
    height: 8rem;
  }
}

#contact {
  margin-top: 10rem;
  margin-bottom: 12rem;
}

#contact h1 {
  margin-left: 10vw;
  margin-right: 10vw;
  font-size: 4rem;
}

#contact p {
  margin-left: 10vw;
  margin-right: 10vw;
  font-size: 2rem;
}
#contact a {
  text-decoration: none;
}

#contact a:hover {
  text-decoration: underline;
}

.bottom-button {
  position: fixed;
  bottom: 2.4rem;
  right: 0.8rem;
  height: 4rem;
}

.bottom-button button {
  z-index: 100;
  font-weight: 600;
  font-size: 2.2rem;
  box-shadow: rgba(17, 17, 26, 0.1) 0px 4px 16px, rgba(17, 17, 26, 0.1) 0px 8px 24px, rgba(17, 17, 26, 0.1) 0px 16px 56px;
  border-radius: 100%;
  border: none;
  outline: none;
  width: 4.6rem;
  height: 4.6rem;
}

.bottom-button button:hover {
  cursor: pointer;
  animation: bounce 2s infinite;
}

@keyframes bounce {
  0% { transform: scale(1,1) translate(0, 0); }
  20% { transform: scale(1,0.8) translate(0, 1.2rem); }
  60% { transform: scale(1,1.1) translate(0, -0.8rem); }
  80% { transform: scale(1,0.8) translate(0, 1.2rem); }
  100% { transform: scale(1,1) translate(0, 0); }
}

.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 3px;
  height: 1.4rem;
  font-size: 1rem;
}

.text-animation-container {
  position: relative;
  font-size: 4.8rem;
  height: 14vh;
  margin-top: 8vh;
  margin-left: 10vw;
  
  @media (max-width: 1220px) {
    font-size: 2.6rem;
  };

  @media (max-width: 940px) {
    font-size: 2.2rem;
  }
}

.hello {
  position: absolute;
  font-weight: 800;
  opacity: 0;
  transform: scale(0) translateX(-50%) translateY(-50%);
}

.show-text .hello {
  opacity: 1;
  transform: scale(1) translateX(-50%) translateY(-50%);
}
